.authorization {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin: 0px auto;
    box-sizing: border-box;
    width: 340px;

    padding: 20px;

    border-radius: 10px;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.6);
}

.authorization h1 {
    margin: 0px 0px 50px;
    font-family: Verdana, sans-serif, 'Inter';
    font-weight: normal;
    color: #000000;
}

.authorization__password {
    margin: 20px 0px 30px;
}

.authorization a {
    margin-top: 10px;
    color: var(--mainColor);
    font-family: Verdana, sans-serif, 'Inter';
    font-size: 12px;
}
